body {
  overflow-x: hidden;
}

.header {
  position: absolute;
  top: 0;
  width: 100%;
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding-bottom: 10px;
}

.bar {
  position: absolute;
  display: block;
  height: 50px;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  bottom: 0;

  transition: 1.0s;
  transition-timing-function: linear;

  .progress {
    background-color: #09d3ac;
    height: 50px;
  }
}

.btn {
  margin-top: 6vh;
  background-color: #09d3ac;
  padding: 1%;
  border-radius: 5px;
  width: 100px;
  cursor: pointer;
  text-align: center;
  color: white;

  &:hover{
    background-color: #39ffdc;
   }
}

.title {
  font-size: 1.8em;
}

a {
  text-decoration: none;
  color: white;
}

.test {
  text-align: center;
  background-color: #191c21;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;

  .object {
    background-color: #ff9d3b;
    width: 100px;
    height: 100px;
    transition: 0.5s;
  }
}


.drop-shadow {
  box-shadow: 10px 10px 10px white;
}

.translate {
  transform: translate(120px,250px);
}